<script>
	import Button from "./Button.svelte"
	import Card_Container from "./Card_Container.svelte"
	import Card from "./Card.svelte"
	import Navigation from "./Navigation.svelte"
	import Add_Container from "./Add_Container.svelte"
	import Form_Group from "./Form-Group.svelte"
	import {card} from './store.js'
	import {onMount} from 'svelte'


	let show = false;
	let question ='';
	let answer = '';
	let currentActive = 0;
	let cards = []



function addData(){
	if(question !== '' && answer !== ''){
	let cardArr = {answer:answer,question:question}
	card.addCard(cardArr)
	question = ''
	answer = ''
	show = false
}else{
	alert('enter something')
}
}
	



function nextCard(){
	cards = document.querySelectorAll('.card')
	cards[currentActive].className = 'card left'
		cards[currentActive].classList.remove('active')
		currentActive++;
	if (currentActive > cards.length - 1) {
    	currentActive = cards.length - 1;
  	}
	cards[currentActive].className = 'card active'
	
		
	// }
	
}

function prevCard(){
	cards = document.querySelectorAll('.card')
	if(currentActive !== 0){
	cards[currentActive].classList.remove('active')
	currentActive--;
	cards[currentActive].className = 'card active'
	} 
}






</script>
<svelte:head>
	<link
      rel="stylesheet"
      href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css"
      integrity="sha256-+N4/V/SbAFiW1MPBCXnfnP9QSN3+Keu+NlB+0ev/YKQ="
      crossorigin="anonymous"
    />
</svelte:head>
<Button id={'clear'} clas={'clear'} icon={'trash'} on:click={card.clearAll} text={'Clear Cards'} />

<h1>Memory Cards
	<Button on:click={()=> show = true} id={'show'} clas={'btn-small'} icon={'plus'} text={'Add New Card'}/>
</h1>

<Card_Container>
<Card />
</Card_Container>

<Navigation on:prev={prevCard} bind:currentActive on:next={nextCard}/>
<Add_Container {show}>
	<h1>
        Add New Card
		<Button on:click={()=> show = false} id={'hide'} clas={'btn-ghost'} icon={'times'} />
	</h1>
<Form_Group bind:question bind:answer />
<Button id={'add-card'}  icon={'plus'} text={'Add Card'} on:click={addData}/>
</Add_Container>
	

<style>
	:global(*){
		margin: 0;
		padding: 0;
	}
	:global(body){
		height: 100vh;
		width: 100%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		
	}

	

	:global(h1){
		position: relative;
		margin-bottom: 30px;
	}



	
	
</style>