<script>
  import {card} from './store.js'
    function setShow(){
      let cards = document.querySelectorAll('.card')
      cards.forEach(card=>{
        if(card.classList.contains('active')){
          card.classList.toggle('show-answer')
        }
      })
    }
</script>
{#if (Array.isArray($card) && Object.keys($card).length > 0)}
{#each $card as data,i }
<div  id="card" class="{i===0? 'card active': 'card'}" >
    <div  class="inner-card"  on:click={setShow} >
      <div class="inner-card-front" >
        <p>
          {data.question}
        </p>
      </div>
      <div class="inner-card-back">
        <p>
          {data.answer}
        </p>
      </div>
    </div>
</div>
{/each}
{/if}


<style>
  :global(.card){
    position: absolute;
    opacity: 0;
    font-size: 1.5em;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    transition: transform 0.4s ease, opacity 0.4s ease;
    
  }
:global(.card.left) {
  transform: translateX(-50%) rotateY(10deg);
}
  :global(.card.active){
    opacity: 1;
    cursor: pointer;
    z-index: 10;
  }

  .inner-card{
    height: 100%;
    width: 100%;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    position: relative;
    transform-style: preserve-3d;
    transition: transform .4s ease-in;
  }
  .inner-card-front,
  .inner-card-back{
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
  }

  .inner-card-front::after,
  .inner-card-back::after{
    content: '\f021  Flip';
    font-family: 'Font Awesome 5 Free';
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 16px;
    font-weight: bold;
    
  }

  :global(.card.show-answer .inner-card){
    transform: rotateX(180deg);
  }

  .inner-card-front{
    transform: rotateX(0deg);
    z-index: 3;
  }

  .inner-card-back{
    transform: rotateX(180deg);
  }
</style>