<script>
    export let id ='';
    export let clas ='';
    export let icon = '';
    export let text ='';

</script>


<button id={id} on:click class="btn {clas}">
    <i class="fas fa-{icon}"></i> {text}
</button>

<style>

    :global(button){
        cursor: pointer;
    }

   
    .clear{
		position: absolute;
		left: 20px;
		bottom: 20px;
	}

    .btn-ghost{
        background-color: transparent;
        border: none;
        background-color: transparent;

    }

    .btn-ghost:active{
        background-color: transparent;
    }

    .btn-small{
        font-size: 1rem;
        transform: translate(120%,5px);
        position: absolute;
        right: 0;
    }



    @media only screen and (max-width: 600px) {
        .btn-small{
        font-size: 1rem;
        transform: translate(0,-5px);
        position: relative;
        right: 0;
    }
}

   .nav-button{
        background-color: transparent;
        border: none;
   }

   .nav-button:active{
       background-color: transparent;
   }
</style>