<script>
    import {card} from './store.js'
    import Button from "./Button.svelte"
    import {createEventDispatcher} from 'svelte'
    const dispatch = createEventDispatcher()

    export let currentActive;
</script>

<div class="navigation">
    <Button on:click="{()=> dispatch('prev')}"  id={'prev'} clas={'nav-button'} icon={'arrow-left'} />
    {#if $card.length > 0}
    <p id="current">{currentActive + 1} / {$card.length}</p>
    {:else}
    <p id="current">{currentActive} / {$card.length}</p>
    {/if}
	<Button on:click="{()=> dispatch('next')}" id={'next'} clas={'nav-button'} icon={'arrow-right'} />
</div>

<style>
    .navigation{
        display: flex;
        margin: 20px 0;
    }
    p{
        margin: 0 25px;
    }

    
</style>